<template>
	<div class="testdetails-container">
		<div class="testdetails-header">
			<div class="testdetails-zhong">
				<div class="testdetails-fanhui">
					<img src="../assets/nav_back.png" @click="back()">
				</div>
				<div class="testdetails-ti">测试详情</div>
				<div></div>
			</div>
		</div>
		<div class="testdetails-content">
			<div class="testdetails-csxq">
				<img src="../assets/banner-ceshixiangqing.png">
			</div>
			<div class="testdetails-guoshi">{{data.eqtest_title}}</div>
			<div class="testdetails-mianfei">免费</div>
			<div class="testdetails-bj">
				<div class="testdetails-csjs">测试介绍</div>
				<div class="testdetails-dute">{{data.eqtest_content}}</div>
			</div>
		</div>
		<div class="testdetails-footer">
			<div class="testdetails-kaishi" @click="go_quiz()">开始测试</div>
		</div>
	</div>
</template>

<script>
	import Qs from 'qs'
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
				eqtest_list: '',
				eqtestid: '1',
				user_info: {},
				userid: '1',
				data: {}
			}
		},
		methods: {
			back(){
				window.history.back();
			},
			go_quiz(){
				// 把 this.eqtestid 带到下一个页面
				this.$router.push('/quiz/' + this.eqtestid);
			},
			eqtest() {
				var that = this;
				var params = {
					userid: that.userid,
					eqtestid: that.eqtestid,
				};
				that.$http.post('/eqtest/eqtest_info', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						that.data = res.data;
					}
				})
			}
		},
		created() {
			// 页面刚加载的时候执行

			// 把带过来的 id 赋值给 this.eqtestid
			this.eqtestid = this.$route.params.id;

			this.eqtest();
			if(!!localStorage.getItem('user_info')) {
				this.user_info = JSON.parse(localStorage.getItem('user_info'));
			}

		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行

		}
	}
</script>

<style scoped>
	.testdetails-container {
		width: 100%;
		height: 100%;
		/*background: #FFFFFF;*/
	}
	
	.testdetails-header {
		height: 50px;
		background: #FFFFFF;
		line-height: 50px;
		/*border-bottom: 1px solid #eaeaea;*/
	}
	
	.testdetails-zhong {
		display: flex;
		justify-content: space-between;
	}
	
	.testdetails-fanhui img {
		width: auto;
		height: 1.3rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}
	
	.testdetails-ti {
		font-size: 1.8rem;
		color: #000000;
		margin-left: -1.5rem;
	}
	
	.testdetails-content {
		width: 100%;
		height: calc(100% - 100px);
		overflow-x: hidden;
		overflow-y: auto;
	}
	.testdetails-csxq img{
		width: 100%;
		height: auto;
		background: #FFFFFF;
	}
	.testdetails-csxq{
		background: #FFFFFF;
	}
	.testdetails-guoshi{
		font-size: 1.6rem;
		color: #000000;
		text-align: center;
		width: 100%;
		background: #FFFFFF;
		padding-top: 1.5rem;
		padding-bottom: 1.5rem;
	}
	.testdetails-mianfei{
		font-size: 1.6rem;
		color: #fd5997;
		text-align: center;
		width: 100%;
		background: #FFFFFF;
		padding-bottom: 1rem;
		margin-bottom: 1.5rem;
	}
	.testdetails-bj{
		background: #FFFFFF;
		padding-top: 1.5rem;
	}
	.testdetails-csjs{
		font-size: 1.5rem;
		color: #000000;
		border-left: 5px solid #fd5997;
		padding-left: 0.8rem;
		margin-left: 1.5rem;
	}
	.testdetails-dute{
		font-size: 1.4rem;
		color: #000000;
		padding: 1.3rem 0 1.5rem 1.6rem;
	}
	.testdetails-footer {
		height: 50px;
		line-height: 50px;
		box-sizing: border-box;
		overflow: hidden;
		position: fixed;
		bottom: 0;
		width: 100%;
		background: #fd5997;
	}
	.testdetails-kaishi{
		font-size: 1.5rem;
		color: #FFFFFF;
		text-align: center;
	}
</style>


